import React from "react";
import { Tab, Tabs, Spinner } from "react-bootstrap";
import Header from "./components/Header";

class App extends React.Component {
  render() {
    const CodeCardContainer = React.lazy(() =>
      import("./components/CodeCardContainer.js")
    );
    const AboutMe = React.lazy(() => import("./components/AboutMe.js"));

    return (
      <div className="App">
        <Header />
        <Tabs
          defaultActiveKey="cheatsheet"
          id="uncontrolled-tab-example"
          style={{ marginBottom: "10px" }}
        >
          <Tab eventKey="cheatsheet" title="Cheat Sheet">
            <React.Suspense
              fallback={<Spinner animation="border" variant="secondary" />}
            >
              <CodeCardContainer />
            </React.Suspense>
          </Tab>
          <Tab eventKey="about" title="About">
            <React.Suspense
              fallback={<Spinner animation="border" variant="secondary" />}
            >
              <AboutMe />
            </React.Suspense>
          </Tab>
        </Tabs>
      </div>
    );
  }
}

export default App;
